.toolbar{
    position: fixed;
    top: 0;
    left: 0 ;
    width: 100%;
    background: #000000;
}

.toolbar_navigation{
    display: flex;
    align-items: center ;
    height: 100%;
    padding: 0 1rem;
}

.toolbar_logo a{
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
}

.toolbar_navigation_items a {
    color: white;
    text-decoration: none;
}

.toolbar_navigation_items a:hover,
.toolbar_navigation_items a:active{
    color: yellow;
}

.toolbar_navigation_items ul{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.toolbar_navigation_items li{
    padding: 0 0.5rem;
}

.spacer{
    flex: 1;
}